import Vuex from 'vuex'
import Vue from 'vue'
import user from './modules/user.js'

Vue.use(Vuex)
const store = new Vuex.Store({
    state:{
        stateList:[

        ]
    },
    mutations:{
        changeState ( state, value ){
            state.stateList = value
        }
    }
})
export default store


