<template>
  <div class="hello">
    <!-- {{$store&&$store.state.stateList}}<br> -->
    Cuurrent status is &lt; {{currentStatusName || 'null'}} &gt
    <input type="button" value="Create Status" @click="openNewPage('create')"><br>
    update status 
    <select name="" id="" v-model="updateSelectState">
      <option v-for="item in $store.state.stateList" :value="item.id" :key="item.id"> {{item.name}} </option>
      <option  value="" disabled hidden selected>select a status</option>
    </select>
    <br>
    <br>
    Could be transferrd to <select name="" id="" v-model="couldBeSelectState">
      <option v-for="item in $store.state.stateList" :value="item.id" :key="item.id"> {{item.name}} </option>
      <option  value="" disabled hidden selected>select a statusd</option>
    </select>?
    <br>
    {{couldBeSelectStateValue}}
    <br>
    <br>
    How could be transferrd to 
    <select name="" id="" v-model="HowToState">
      <option v-for="item in $store.state.stateList" :value="item.id" :key="item.id"> {{item.name}} </option>
      <option  value="" disabled hidden selected>select a status</option>
    </select>
    <br>
    {{pathToState}}
    <br>
    <br>
    Transferrd to 
    <select name="" id="" v-model="toSelectState">
      <option v-for="item in $store.state.stateList" :value="item.id" :key="item.id"> {{item.name}} </option>
      <option  value="" disabled hidden selected >select a status</option>
    </select>
    <!-- <input type="button" value="更新状态" @click="openNewPage('update')"><br> -->

    
    
  </div>
</template>

<script>
export default {
  name: 'mainpage',
  data () {
    return {
      currentStatus: null,
      currentStatusName: null,
      updateSelectState:'', // 更新
      couldBeSelectState:'', // 是否能选某状态
      couldBeSelectStateValue:'', //yes / no
      toSelectState:'', // 转换到某状态
      pathToState:'',
      HowToState:'', //如何转换
      changeState:'', //如何转换
    }
  },
  methods:{
    openNewPage(str){
      let obj = this.$tools.findElement(this.$store.state.stateList, o=> o.id == this.updateSelectState)
      this.$router.push(({
          name: str,
          params: {
            state: obj
          }
        }))
    },
    HowToChange(endState, type = 'path'){
      if (!this.currentStatus){
        return this.$tools.findElement(this.$store.state.stateList, o=> o.id == endState).name
      }
      let stateList = {}
      for (let i of this.$store&&this.$store.state.stateList){ // 简化结构
       
        stateList[i.id] = i.changeable
      }
      let findRoad = new this.$tools.FindRoad(stateList); // 生成转换路径
      let start = this.currentStatus// id
      let end = endState // id

      let path = findRoad.hasPath(start,end) // 输出转换路径
      console.log(findRoad.hasPath(start,end))
      return this.translate(type, path) // 翻译输出结果
    },
    translate(type, result){ // 将翻译转换成显示的数据 Boolean:是否有结果 path:转换路径 
      console.log(type)
      console.log(result)
        debugger
      if(result.length==0){
        return false
      }
      if(type === 'Boolean'){
        return result && true
      }
        debugger
      if (type === 'path'){
        debugger
        let path = []
        result.forEach((item,index)=>{
          let name = this.$tools.findElement(this.$store.state.stateList, o=> o.id == item).name
          debugger
          path.push(!index?name:`=> ${name}`)
        })
        console.log(path.join(''))
        return path.join('')
      }
    }
  },
  watch:{
      'updateSelectState':function(){ //更新状态关系
        this.openNewPage('update')
      },
      'toSelectState':function(){ // 转换到某状态
        if(!this.currentStatus){ // 如果当前无状态，就可以转换为任何一种状态
          this.currentStatus = this.toSelectState
          this.currentStatusName = this.$tools.findElement(this.$store.state.stateList, o=> o.id == this.currentStatus).name
        } else {
          if(this.HowToChange(this.toSelectState,'Boolean')){
            this.currentStatus = this.toSelectState
            this.currentStatusName = this.$tools.findElement(this.$store.state.stateList, o=> o.id == this.currentStatus).name
          } else {
            alert(`can not transferred to <${this.$tools.findElement(this.$store.state.stateList, o=> o.id == this.toSelectState).name}>`)
          }
        }
      }, // 是否能选
      couldBeSelectState:function(){
        let couldBeSelectStateName = this.$tools.findElement(this.$store.state.stateList, o=> o.id == this.couldBeSelectState).name
        if(!this.currentStatus){ // 如果当前无状态，就可以转换为任何一种状态
          this.couldBeSelectStateValue = `Yes,can be transferred to <${couldBeSelectStateName}> from <${this.couldBeSelectStateName || 'null'}>`
        } else {
          if(this.HowToChange(this.couldBeSelectState,'Boolean')){
            this.couldBeSelectStateValue = `Yes,can be transferred to <${couldBeSelectStateName}> from <${this.couldBeSelectStateName || 'null'}>`
          } else {
            this.couldBeSelectStateValue = `No,can not transferred to <${couldBeSelectStateName}> from <${this.couldBeSelectStateName || 'null'}>`
          }
        }

      }, //如何转换
      HowToState:function(){
          if(this.HowToChange(this.HowToState,'Boolean')){
            debugger
            this.pathToState = this.HowToChange(this.HowToState,'path')
          } else {
            this.pathToState = 'Could be transferrd to <'+ this.$tools.findElement(this.$store.state.stateList, o=> o.id == this.HowToState).name + '>'
          }
      }, //如何转换
  },
  created(){
    // this.translate()
  }
}
</script>

<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}

</style>



