import Vue from 'vue'
const deepClone = (element) => { 
    if (typeof element === 'object') {
        return JSON.parse(JSON.stringify(element))
    } else {
        return element
    }
}

const hasElement = (arr,fun) => {  // 判断是否有当前条件的数据
    if (typeof arr === 'object') {
        for (let num in arr) {
            if(fun(arr[num])){
                return num 
            }
        }
        return -1
    } else {
        return -1
    }
}

const findElement = (arr,fun) => {
    if (typeof arr === 'object') {
        for (let num in arr) {
            if(fun(arr[num])){
                return arr[num] 
            }
        }
        return {}
    } else {
        return {}
    }
  }
const findElementIndex = (arr,fun) => {
  if (typeof arr === 'object') {
    let eleIndex = -1
    arr.forEach((item, index) => {
      if(fun(item)){
        eleIndex = index
      }
    });
    return eleIndex
  } else {
      return -1
  }
}

class FindRoad {
    constructor (allrelation) { // 所有状态，状态之间的关系
        this.allrelation = allrelation // 所有关系
        this.allStates = Object.keys(allrelation),// 所有状态
        this.result = [],// 最短路径
        this.hasResult = false // 是否可以转换到目标状态
    }
    findShortPath(relation, notes, goal){
        if (!relation) { // 如果没有关系有，跳出当前
            console.log('没有关系了，跳出当前递归')
            return
        }
        if (this.hasResult && this.result.length < notes.length && this.result.length!=0){// 已有路径比当前路径短则退出
            console.log('已找到合适路径，并且比当前路径短')
            return
        }
        for(let i in relation){
            console.log(relation[i])
            if(notes.includes(relation[i])){ // 如果这个状态已经走过 就继续走下一个
                continue
            }
            let newNotes = deepClone(notes)
            newNotes.push(relation[i])
            // 
            if(relation.includes(goal) ){
               console.log('path')
                this.hasResult = true
                if( this.result.length === 0 || newNotes.length < this.result.length){
                    this.result = newNotes
                }
                return
            }else {
                this.findShortPath(this.allrelation[relation[i]],newNotes,goal)
            }
        }

    }
    hasPath(a,b){// a:起始点，b：目标点
        this.findShortPath(this.allrelation[a], [a], b)
        return this.result
    }
    getResult(a,b){
        hasPath(a,b)
    }
}


