<template>
  <div class="hello">
    <router-link to="/">main page</router-link>
    <h2>更新页面</h2>
    Status name : <{{currentStatus.name}}> <a @click="deleteStatus" style="cursor: pointer; color: blue;">delete</a>  <br>
    可选状态：<br>
    <div class="stateList">
      <div v-for="state in stateList" :key="state.id" >
        <input type="checkbox" v-model="changeable" name="state2" :value="state.id" :id="state.id">
        <label for="state.id">{{state.name}}</label>
      </div>
    </div>
    
    <input type="button" value="Update Status" @click="updateState">
  </div>
</template>

<script>
export default {
  name: 'updatePage',
  data () {
    return {
      currentStatus: {},//'当前状态',
      stateList:[], // 状态列表
      changeable:[]
    }
  },
  methods:{
    deleteStatus(){
      let index = this.$tools.findElementIndex(this.stateList, o=> o.id == this.currentStatus.id)
      debugger
      this.stateList.splice(index, 1)
      this.$store.commit('changeState',this.stateList)
      alert('Deleted successfully')
      this.$router.push('/')
    },
    updateState(){
      let n = this.$tools.hasElement(this.stateList, o=> o.id == this.currentStatus.id)
      this.currentStatus.changeable = this.changeable
      this.stateList.splice( n, 1, this.currentStatus);
      this.$store.commit('changeState',this.stateList) // 
      this.$router.push('/')
    }
  },
  created(){

  },
  activated(){
    this.currentStatus = this.$route.params.state
    this.stateList = this.$tools.deepClone(this.$store.state.stateList)
    this.changeable = this.currentStatus.changeable
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}

</style>



