<template>
  <div class="hello">
    <router-link to="/">main page</router-link>
    <h2>Create Status</h2>
    Status name： <input type="text" name="" v-model="stateName" id=""><br>
    Could be transferrd to：<br>
    <div class="stateList">
      <div v-for="state in stateList" :key="state.id" >
        <input type="checkbox" v-model="changeable" name="state" :value="state.id" :id="state.id">
        <label for="state.id">{{state.name}}</label>
      </div>
    </div>
    <input type="button" value="Create Status" @click="createState">
    <!-- 数据：{{$store && $store.state.stateList}}<br> -->
  </div>
</template>

<script>
export default {
  name: 'createPage',
  data () {
    return {
      currentStatus: "",//'当前状态',
      stateName:'',
      changeable:[], // 可转换状态
      stateList:[], // 状态列表
    }
  },
  methods:{

    createState(){
      if(!this.stateName){
        alert('状态名称不能为空')
        return false
      }
      if(this.hasElement(this.stateList, o=>o.name == this.stateName) >= 0){
        alert('已存在此状态名称')
        this.stateName = ''
        this.changeable = ''
        return false
      }
      let newState = {
        id: Date.parse(new Date()),
        name: this.stateName,
        changeable:this.changeable
      }
      this.stateList.push(newState)
      this.stateName = []
      this.changeable = []
      this.$store.commit('changeState',this.stateList)
      this.$router.push('/')
    },
    hasElement(arr,fun)  {  // 判断是否有当前条件的数据
      if (typeof arr === 'object') {
          for (let num in arr) {
              if(fun(arr[num])){
                  return num 
              }
          }
          return -1
      } else {
          return -1
      }
    },
    commitDate(){
    },
    
  },
  created(){
  },
  activated (){
    this.stateList = this.$tools.deepClone(this.$store.state.stateList)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}

</style>



